import { useState } from 'react';
import { Row } from 'reactstrap';
import moment from 'moment';
import ReactDatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

const ProfileDetails = (props) => {
  let calendar = null;

  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(null);
  const onChange = (dates) => {
    const [start, end] = dates;
    let startDate = moment(start).format('YYYY-MM-DD');
    let endDate = moment(end).format('YYYY-MM-DD');
    setStartDate(start);
    setEndDate(end);
    if (moment(endDate).diff(startDate, 'days') + 1 > 7) {
      alert('Please select Range of 7 days!');
      return;
    }
    props.changeData(startDate, endDate);
  };

  const openDatepicker = () => calendar.setOpen(true);

  return (
    <Row className="profileDetails p-2 mx-0 pl-4 align-items-center">
      <h4 className="font-weight-bold mb-0">Allen, Joe</h4>
      <h3 className="attendanceRate ml-3 mb-0 font-weight-bold">{'98.9%'}</h3>
      <h4 className="ml-3 pt-2 mb-0" style={{ fontSize: '18px' }}>Attendance Rate</h4>
      <h3 className="attendanceRate ml-3 mb-0 font-weight-bold">{'0.67%'}</h3>
      <h4 className="ml-3 pt-2 mb-0" style={{ fontSize: '18px' }}>Computer Idle Time</h4>
      <div className="ml-auto mr-2 d-flex align-items-center">
        Date: &nbsp; <ReactDatePicker
          ref={(c) => calendar = c}
          selected={startDate}
          onChange={onChange}
          startDate={startDate}
          endDate={endDate}
          selectsRange
        />
        <i onClick={openDatepicker} className="dateCalendar fa fa-lg font-weight-bold fa-calendar"></i>
      </div>
    </Row>
  );
}

export default ProfileDetails;