import React from 'react';
import { exampleData } from './exampleData';
import Header from './Components/Header/Header';
import ProfileDetails from './Components/ProfileDetails/ProfileDetails';
import { minsToTimeStr } from "./utils/time-util";
import './App.scss'
import moment from 'moment';

const timeStrToMins = (strTime) => {
  if (!strTime) return 1;
  let str = moment(strTime, 'hh:mm A').format('HH:mm');
  return moment.duration(str).asMinutes();
}

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      startDate: '',
      endDate: '',
      weekOHTAData: [],
      weekCLAData: [],
      weekHolidayData: [],
      weekPTOData: [],
      ohtaDayTotal: [],
      totalMinsByDay: [],
      claDayTotal: [],
      totalMinsByWeek: 0,
      ohtaTotal: 0,
      claTotal: 0,
      ptoTotal: 0,
    };
  }

  componentDidMount() {
    this.onChangeOfDate();
  }

  getTimingData = (dataArr, start, end) => {
    let arr = [], dayTotal = [], total = 0.0;
    dataArr.map((data) => {
      if (moment(data.date).isSame(start) || moment(data.date).isSame(end)
        || moment(data.date).isBetween(start, end)) {
        arr.push(data);
        let val = this.getSummaryValue(data);
        dayTotal.push(val ? minsToTimeStr(val) : 0);
        total = total + Number(minsToTimeStr(val));
      }
    });
    return { arr, dayTotal, total };
  }

  onChangeOfDate = (start = moment().format('YYYY-MM-DD'), end = moment().add(6, 'days').format('YYYY-MM-DD')) => {
    const { weekOHTAData, weekCLAData, weekPTOData, weekHolidayData } = this.state;
    let ohtaData = (start || end) ? [] : weekOHTAData;
    let claData = (start || end) ? [] : weekCLAData;
    let ptoData = (start || end) ? [] : weekPTOData;
    let holidayData = (start || end) ? [] : weekHolidayData;
    let ohtaDayTotal = [];
    let ptoDayValue = [];
    let claDayTotal = [];
    let totalMinsByDay = [];
    let ohtaTotal = 0, claTotal = 0, ptoTotal = 0, totalMinsByWeek = 0;

    let ohtaTimeData = this.getTimingData(exampleData.ohtaData, start, end);
    ohtaData = ohtaTimeData.arr;
    ohtaDayTotal = ohtaTimeData.dayTotal;
    ohtaTotal = ohtaTimeData.total;

    let claTimeData = this.getTimingData(exampleData.claData, start, end);
    claData = claTimeData.arr;
    claDayTotal = claTimeData.dayTotal;
    claTotal = claTimeData.total;

    exampleData.ptoData.map((pto) => {
      if (moment(pto.date).isSame(start) || moment(pto.date).isSame(end)
        || moment(pto.date).isBetween(start, end)) {
        ptoData.push(pto);
        ptoDayValue.push(Number(pto.value) || 0.0);
        ptoTotal = Number(ptoTotal) + Number(pto.value || 0);
      }
    });
    exampleData.holidayData.map((hol) => {
      if (moment(hol.date).isSame(start) || moment(hol.date).isSame(end)
        || moment(hol.date).isBetween(start, end)) {
        holidayData.push(hol);
      }
    })
    totalMinsByDay = [ohtaDayTotal, claDayTotal].reduce((r, a) => a.map((b, i) => Number(r[i] || 0) + Number(b)), []);
    totalMinsByWeek = (Number(ohtaTotal) + Number(claTotal) + Number(ptoTotal)).toFixed(2);
    this.setState({
      startDate: start, endDate: end,
      weekOHTAData: ohtaData, weekCLAData: claData, weekPTOData: ptoData, ptoDayValue, weekHolidayData: holidayData,
      ohtaTotal: Number(ohtaTotal).toFixed(2), claTotal: Number(claTotal).toFixed(2), ptoTotal, ohtaDayTotal, claDayTotal, totalMinsByDay, totalMinsByWeek
    });
  }

  getSummaryValue = (data) => {
    let summaryVal = timeStrToMins(data.outTime_2) - timeStrToMins(data.inTime_1) - (timeStrToMins(data.inTime_2) - timeStrToMins(data.outTime_1));
    if (!summaryVal) summaryVal = 0;
    summaryVal = Math.max(summaryVal, 0);
    return summaryVal;
  }

  render() {
    const { weekOHTAData, weekCLAData, claTotal, ohtaTotal, weekHolidayData, weekPTOData, totalMinsByWeek,
      ptoTotal, totalMinsByDay, startDate, endDate, ohtaDayTotal, claDayTotal } = this.state;
    const emp_Details = [
      { label: 'NAME:', value: exampleData.emp_name },
      { label: 'BRANCH:', value: exampleData.branch },
      { label: 'DEPARTMENT:', value: exampleData.department },
      { label: 'PERIOD:', value: `${moment(startDate).format('MM/DD/YYYY')}${endDate !== 'Invalid date' ? ` - ${moment(endDate).format('MM/DD/YYYY')}` : ''}` },
    ]
    return (
      <div className="App">
        <Header />
        <br />
        <ProfileDetails changeData={(start, end) => this.onChangeOfDate(start, end)} />
        <br />
        <div className="row">
          <div className="col-6">
            <table border="1" cellPadding="0" cellSpacing="0" className="header-table w-100">
              <tbody className="text-center">
                <tr className="bg-blue text-white">
                  <td colSpan="2">Joe Allen's Accumulated Time</td>
                </tr>
                <tr>
                  <td>Week ({moment(startDate).format('LL')}{endDate !== 'Invalid date' ?  `- ${moment(endDate).format('LL')}` : null})</td>
                  <td>Pay period(Apr 17, 2021 - Apr 30, 2021)</td>
                </tr>
                <tr className="text-blue font-weight-bold">
                  <td>{totalMinsByWeek} Hrs</td>
                  <td>76.25 Hrs</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="col-3" />
          <div className="col-3 text-right">
            <div className="row">
              <div className="col-8 px-0">
                <select id="cars" name="carlist" form="empForm" className="height35 w-75">
                  <option value="volvo">OHTA (948)</option>
                </select>
              </div>
              <div className="col-4">
                <button className="btn btn-danger btn-sm height35 w-100"> Clock Out </button>
              </div>
            </div>
            <div className="row mt-2 attendanceRate">
              <div className="col-4">
                {moment().format('h:mm:ss')}
              </div>
              <div className="col-8">
                Start time: {moment().format('h:mm:ss A MMMM DD, YYYY')}
              </div>
            </div>
          </div>
        </div>
        <table border={1} className="w-100 header-table">
          <thead className="text-center">
            <tr className="bg-blue text-white p-2">
              <td colSpan="10" className="text-left pl-2 fs25 font-weight-bold" >WEEKLY TIMESHEET</td>
            </tr>
            {emp_Details.map((x, i) => {
              return <tr key={i}>
                <td className="text-right pr-2 font-weight-bold">{x.label}</td>
                <td colSpan="9" className="text-left pl-2">{x.value}</td>
              </tr>
            })}
            <tr>
              <td colSpan={2}></td>
              {weekOHTAData.map((x, i) => {
                let day = moment(new Date(x.date)).format('ddd');
                let date = moment(new Date(x.date)).format('MMMM DD, YYYY');
                return <td key={i} className="text-center text-blue row-background"><span>{day}</span><br />{date}</td>
              })}
              <td className="text-blue row-background">Total Hours</td>
            </tr>
          </thead>
          <tbody className="text-center">
            <tr>
              <td rowSpan={4} className="font-weight-bold">OHTA</td>
              <td>In</td>
              {weekOHTAData.map((x, i) => <td key={i}>{x.inTime_1}</td>)}
              <td />
            </tr>
            <tr>
              <td>Out</td>
              {weekOHTAData.map((x, i) => <td key={i}>{x.outTime_1}</td>)}
              <td />
            </tr>
            <tr>
              <td>In</td>
              {weekOHTAData.map((x, i) => <td key={i}>{x.inTime_2}</td>)}
              <td />
            </tr>
            <tr>
              <td>Out</td>
              {weekOHTAData.map((x, i) => <td key={i}>{x.outPunch_2 ? <span className="ptoCircle">{x.outPunch_2}</span> : null} {x.outTime_2}</td>)}
              <td />
            </tr>
            <tr className="row-background font-weight-bold">
              <td colSpan={2}>OHTA Hours</td>
              {ohtaDayTotal.map((x, i) => {
                return <td key={i} className="text-blue">{x ? x : ''}</td>
              })}
              <td>{ohtaTotal}</td>
            </tr>
            <tr>
              <td rowSpan={4} className="font-weight-bold">CLA</td>
              <td>In</td>
              {weekCLAData.map((x, i) => <td key={i}>{x.inTime_1}</td>)}
              <td />
            </tr>
            <tr>
              <td>Out</td>
              {weekCLAData.map((x, i) => <td key={i}>{x.outTime_1}</td>)}
              <td />
            </tr>
            <tr>
              <td>In</td>
              {weekCLAData.map((x, i) => <td key={i}>{x.inTime_2}</td>)}
              <td />
            </tr>
            <tr>
              <td>Out</td>
              {weekCLAData.map((x, i) => <td key={i}>{x.outTime_2}</td>)}
              <td />
            </tr>
            <tr className="row-background font-weight-bold">
              <td colSpan={2}>CLA Hours</td>
              {claDayTotal.map((x, i) => {
                return <td key={i}>{x ? x : ''}</td>
              })}
              <td>{claTotal}</td>
            </tr>
            <tr>
              <td colSpan={2} className="font-weight-bold">Holiday</td>
              {weekHolidayData.map((x, i) => {
                return <td key={i}></td>
              })}
              <td />
            </tr>
            <tr>
              <td colSpan={2} className="font-weight-bold">PTO</td>
              {weekPTOData.map((x, i) => {
                return <td key={i}>{x.ptoOff ? <span className="ptoCircle">{x.ptoOff}</span> : null} {x.value}</td>
              })}
              <td className="font-weight-bold">{ptoTotal}</td>
            </tr>
            <tr>
              <td colSpan={2} className="font-weight-bold">Total Hours</td>
              {totalMinsByDay.map((x, i) => {
                return <td key={i}>{x ? Number(x).toFixed(2) : ''}</td>
              })}
              <td className="font-weight-bold">{totalMinsByWeek}</td>
            </tr>
          </tbody>
        </table>
        <table border={1} className="w-100 header-table">
          <thead className="text-center">
            <tr className="bg-blue text-white p-2">
              <td colSpan="10" className="fs25" >Timesheet Changes</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              {weekPTOData.map((x, i) => x.ptoOff ? <td colSpan={4}> <span className="my-1 ptoCircle text-center mr-2">{x.ptoOff}</span>Paid Time off (PTO) {moment(x.date).format('dddd (ll)')}: PTO Added -- Change made by John Manager on 01:58 PM Apr 29, 2018</td> : null)}
            </tr>
            <tr>
              {weekOHTAData.map((x, i) => x.outPunch_2 ? <td colSpan={4}> <span className="my-1 ptoCircle text-center mr-2">{x.outPunch_2}</span>OHTA Out Punch {moment(x.date).format('dddd ll')}: Updated Last Punch -- Change made by John Manager on 04:58 PM Apr 29, 2018</td> : null)}
            </tr>
            <tr style={{ height: '70px' }}>
              <td width="30%">
                <img src="images/signin.png" className="float-right pr-3" alt="sign" />
                <span className="font-weight-bold">Employee Signature:</span>
              </td>
              <td>
                <span className="font-weight-bold">Date :</span>
              </td>
              <td width="30%">
                <span className="font-weight-bold">Approval Signature :</span>
              </td>
              <td>
                <span className="font-weight-bold">Date :</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default App;
